import React from "react";
import imageStart from '../img/phone.png'
import {
    Div,
    Group,
    FormLayout,
    File,
    Tooltip,
    Button
    
} from '@vkontakte/vkui';
import Icon24Camera from '@vkontakte/icons/dist/24/camera';
import './Canvas.css';
import Icon24Delete from '@vkontakte/icons/dist/24/delete';


class ReactCanvas extends React.Component {

    constructor(props) {
        super(props);
        this.state = {

            myImage: this.props.selectedImage ? this.props.selectedImage : imageStart,
            tooltip: true
            
        };
        this.canvas = null;
        this.ctx = null;
        this.ctxImage = new Image();
        this.onImageChange = this.onImageChange.bind(this);
    }

    componentDidMount() {
        const canvasWidth = window.innerWidth - 40;
        const fontSize = canvasWidth * 0.1;
        this.ctxImage.onload = () => {
            this.fontSize = fontSize;
            this.canvas = document.getElementById('memesCanvas');
            this.canvas.width = canvasWidth;
            this.canvas.height = canvasWidth;
            this.ctx = this.canvas.getContext('2d');
            this.canvasDrow(this.ctxImage);
            if(this.props.imageObject.memtype){
              this.ctx.font = `${fontSize*this.props.imageObject.size}px ${this.props.imageObject.fontmem}`;
            this.ctx.fillStyle = `${this.props.imageObject.color}`;
            this.ctx.textBaseline = "hanging";
            this.ctx.textAlign = "center";
          } else {
            this.ctx.font = `${fontSize}px ${this.props.changeFonts}`;
            this.ctx.fillStyle = `${this.props.changeColors}`;
            this.ctx.textBaseline = "hanging";
            this.ctx.textAlign = "center";
          }
            this._redrawCanvas();
        };
        this.ctxImage.src = this.state.myImage;
    }

    componentDidUpdate() {
     this.componentDidMount();
    }

    _redrawCanvas() {
       // this.canvasDrow(this.ctxImage);
      if(this.props.imageObject.memtype) {
        this.ctx.drawImage(this.ctxImage, 0, 0, this.canvas.width, this.canvas.height);
        this.wrapText(this.ctx, this.props.lowText, this.canvas.width * this.props.imageObject.x, this.canvas.height*this.props.imageObject.y, this.canvas.width*this.props.imageObject.maxwidth, this.fontSize*this.props.imageObject.lineheight, false);
      } else {


        this.ctx.drawImage(this.ctxImage, 0, 0, this.canvas.width, this.canvas.height);
        this.wrapText(this.ctx, this.props.topText, this.canvas.width * 0.5, 5, this.canvas.width, this.fontSize, false);
        this.wrapText(this.ctx, this.props.lowText, this.canvas.width * 0.5, this.canvas.height - 5 - this.fontSize, this.canvas.width, this.fontSize, true);
    
        
      }
      
      
    }

    canvasDrow(img) {
        let rotate = this.canvas.width / img.width;
        let imgHeight = rotate * img.height;
        this.canvas.height = imgHeight;

        
        this.ctx.drawImage(img, 0, 0, this.canvas.width, this.canvas.height)

    }


    wrapText(context, text, x, y, maxWidth, lineHeight, fromBottom) {

        var pushMethod = (fromBottom) ? 'unshift' : 'push';

        lineHeight = (fromBottom) ? -lineHeight : lineHeight;

        var lines = [];
        //var y = y;
        var line = '';
        var words = text.split(' ');

        for (var n = 0; n < words.length; n++) {
            var testLine = line + ' ' + words[n];
            var metrics = context.measureText(testLine);
            var testWidth = metrics.width;

            if (testWidth > maxWidth) {
                lines[pushMethod](line);
                line = words[n] + ' ';
            } else {

                line = testLine;

            }
        }
        lines[pushMethod](line);

        for (var k in lines) {
            this.ctx.strokeText(lines[k], x, y + lineHeight * k);
            this.ctx.fillText(lines[k], x, y + lineHeight * k);
        }


    }

    onImageChange(event) {

        let reader = new FileReader();
        reader.onload = (e) => {
            let img = new Image();

            img.onload = (e) => {
                this.canvasDrow(img)
                let kof=img.width/img.height
                console.log(kof)
                

            }
            

            img.src = e.target.result;
            this.setState({
                myImage: e.target.result
            });



        };
        reader.readAsDataURL(event.target.files[0]);

    }


    render() {
        const a = 2;
        return (
            <Div>
                <Group title="Ваш мем">
                    <canvas id='memesCanvas'/>
                </Group>
                <FormLayout>
                    {this.props.isShowUploadButton && <File accept="image/*" top="Загрузите картинку" before={<Icon24Camera/>} size="xl" onChange={this.onImageChange}>
                        Добавить из галереи
                    </File>}

                    <Button value="click" top="" before={<Icon24Delete/>} size="xl" onClick={()=>{
                     this.props.onClear();
                     //this.componentDidMount();
                     this.setState({
                            myImage: imageStart
                        });   
                    
                    }}>
                        Очистить
                    </Button>


                    
                   
                </FormLayout>

                

            </Div>
        );
    }
}

export default ReactCanvas;


