import React from 'react';
import {Panel, Input, Button, FormLayoutGroup, PanelHeader, FormLayout, ScreenSpinner, Select, Tooltip} from '@vkontakte/vkui';
import Icon24Download from '@vkontakte/icons/dist/24/download';
import ReactCanvas from './ReactCanvas';
import connect from '@vkontakte/vkui-connect';
import imageStart from '../img/phone.png'


class Create extends React.Component {

    constructor(props) {
        super(props);

        //console.log(this.props.app);
        this.props.app.setState({
            createState: this
        });

        this.state = {
            topText: '',
            lowText: '',
            fontsStyle: 'ImpactMem',
            colorsStyle: 'white',
            tooltip: true,
            tooltip2: true

            
          
        };

        this._handleLowInputChange = this._handleLowInputChange.bind(this);
        this._handleTopInputChange = this._handleTopInputChange.bind(this);
        this.changeFonts = this.changeFonts.bind(this);
        this.changeColors = this.changeColors.bind(this);
        this.onClear = this.onClear.bind(this);

       
    }

    componentDidMount() {
   if (localStorage.getItem("ToolCreate") === null) {
    this.setState({tooltip: true})
    } else this.setState({tooltip: false})
    if (localStorage.getItem("ToolCreate") === null) {
    this.setState({tooltip2: true})
    } else this.setState({tooltip2: false})
  }

    _handleTopInputChange(event) {
        this.setState({
            topText: event.target.value
        })
    }

    _handleLowInputChange(event) {
        this.setState({
            lowText: event.target.value
        })
    }

    _handleSaveButton() {
        // todo показать крутялку
        this.props.app.setState({popout: <ScreenSpinner/>});
        let canvas = document.getElementById('memesCanvas');
        canvas.toBlob((result) => {
            console.log(result);
            this.props.app.uploadFile(result);
        }, 'image/jpeg');
      }



  changeFonts(e) {
    switch (e.target.value) {
      case "1": this.setState({
        fontsStyle: 'CollectMem'});
      break;
       case "2": this.setState({
        fontsStyle: 'AmericanMem'});
      break;
      
       case "3": this.setState({
        fontsStyle: 'GagalinMem'});
      break;
       
       case "4": this.setState({
        fontsStyle: 'CompactMem'});
      break;
     default: 
       this.setState({
        fontsStyle: 'ImpactMem'
      }) 
      }   
  }
  changeColors(e) {
    switch (e.target.value) {
      case "1": this.setState({
        colorsStyle: 'black'});
      break;
      case "2": this.setState({
        colorsStyle: 'yellow'});
      break;
      case "3": this.setState({
        colorsStyle: 'blue'});
      break;
      case "4": this.setState({
        colorsStyle: 'red'});
      break;
      case "5": this.setState({
        colorsStyle: 'green'});
      break;
      case "6": this.setState({
        colorsStyle: 'orange'});
      break;
       case "7": this.setState({
        colorsStyle: '#5A009D'});
      break;
     default: 
       this.setState({
        colorsStyle: 'white'
      }) 
      }   
  }

onClear(){
this.props.app.setState({photoObject: {
              photo:{
                
  },
              
            }});
 this.setState({
        topText:'',
        lowText:'',
      });
 this.props.app.setState({
  selectedImage: imageStart
 })
  }
        
    
  


    render() { 
      let PanelActiv = <DefaultMem create = {this} photo={this.props.app.state.photoObject.photo} onClear={this.onClear}/>
      //console.log(this.props.app.state.photoObject)

      if(this.props.app.state.photoObject.photo.memtype == "comic one") {
        PanelActiv = <OneMem create = {this} photo={this.props.app.state.photoObject.photo} onClear={this.onClear}/>
      }

        return (
            <Panel id={this.props.id}>
                <PanelHeader>
                    Создать Мем
                </PanelHeader>

                {PanelActiv}
            </Panel>
        )
    }

}
const DefaultMem = (props)=>{
 return <main>
 <Tooltip
                text="Можно использовать не только шаблоны, но и свои фото или картинки!"
                isShown={props.create.state.tooltip}
                onClose={() => {props.create.setState({ tooltip: false })
                localStorage.setItem("ToolCreate", 1)

              }}
                offsetX={60}
              >
              
              <div>
                    <ReactCanvas topText={props.create.state.topText} 
                                lowText={props.create.state.lowText}
                                 selectedImage={props.create.props.selectedImage}
                                 changeFonts = {props.create.state.fontsStyle}
                                 changeColors = {props.create.state.colorsStyle}
                                 isShowUploadButton = {true}
                                imageObject = {props.photo}
                                onClear={props.onClear}


                                 />
                                 </div>
                                 </Tooltip>
                                     
                    <FormLayout>
                        <FormLayoutGroup top="Введите текст">

                            <Input maxLength='60' value={props.create.state.topText} onChange={props.create._handleTopInputChange}
                                   type="text" placeholder="Текст сверху"/>

                            <Input maxLength='60' value={props.create.state.lowText} onChange={props.create._handleLowInputChange}
                            
                                   type="text" placeholder="Текст снизу"/>
                                   

                        </FormLayoutGroup>

                        <Select style = {{fontFamily: 'ImpactMem'}} onChange = {props.create.changeFonts} top="Изменить шрифт" placeholder="Impact">
                            <option value="1" style = {{fontFamily: 'CollectMem'}}>Collect</option>
                            <option value="2" style = {{fontFamily: 'AmericanMem'}}>American</option>
                            <option value="3" style = {{fontFamily: 'GagalinMem'}}>Gagalin</option>
                            <option value="4" style = {{fontFamily: 'CompactMem'}}>Compact</option>
                            
                             </Select>
                        <Select onChange = {props.create.changeColors} top="Изменить цвет" placeholder="Белый">
                            <option value="1">Черный</option>
                            <option value="2">Желтый</option>
                            <option value="3">Синий</option>
                            <option value="4">Красный</option>
                            <option value="5">Зеленый</option>                            
                            <option value="6">Оранжевый</option>
                            <option value="7">Фиолетовый</option>

                            
                             </Select>     
                                               
                        <Button level="commerce" size="xl" 
                                onClick={() => props.create._handleSaveButton()}>Сохранить</Button>
                              
                    </FormLayout>

                </main>;
}
const OneMem = (props)=>{
 return <main>
 <Tooltip
                text="Не понравился шаблон? Жми кнопку 'Очистить' и начинай заново!"
                isShown={props.create.state.tooltip2}
                onClose={() => {props.create.setState({ tooltip2: false })
                localStorage.setItem("ToolCreateOne", 1)

              }}
                offsetX={60}
              >
              <div>
                    <ReactCanvas topText={props.create.state.topText} lowText={props.create.state.lowText}
                                 selectedImage={props.create.props.selectedImage}
                                 changeFonts = {props.create.state.fontsStyle}
                                 changeColors = {props.create.state.colorsStyle}
                                 isShowUploadButton = {false}
                                 imageObject = {props.photo}
                                onClear={props.onClear}


                                 />
                                 </div>
                                 </Tooltip>
                    <FormLayout>
                        <FormLayoutGroup top="Введите текст">
                            
                            <Input maxLength='30' value={props.create.state.lowText} onChange={props.create._handleLowInputChange}
                            
                                   type="text" placeholder="Текст"/>

                        </FormLayoutGroup>
                       
                                               
                        <Button level="commerce" size="xl" 
                                onClick={() => props.create._handleSaveButton()}>Сохранить</Button>
                    </FormLayout>

                </main>;
}

export default Create;



