import React from 'react';
import {Panel, Div, PanelHeader,Button, FormLayout, FormLayoutGroup, ScreenSpinner} from '@vkontakte/vkui';
import connect from "@vkontakte/vkui-connect/index";
import './Mymem.css';
import Icon24Share from '@vkontakte/icons/dist/24/share';

const Mem = (props) => {
    return <Div key={props.key}><img className = "memcss" src={props.image} alt=""/><Button before={<Icon24Share/>} className = "btncss"data-src={props.image} data-attachments={props.attachments} onClick={e=>{
    	//console.log(e, e.target,e.currentTarget);
    	//connect.send("VKWebAppShare", {"link":e.currentTarget.getAttribute('data-attachments')});
    	//connect.send("VKWebAppShare", {"link":''});
    	connect.send("VKWebAppShowWallPostBox", {"message": "Создаю мемы тут - https://vk.com/app6787772", "attachments":e.currentTarget.getAttribute('data-attachments')});
    }} level="commerce" size="xl">Поделиться</Button>
</Div>;
};

class Mymem extends React.Component {
    constructor (props) {
            super(props)


    this.state = {
      popout: null
    }
  }

    componentDidMount() {
        console.log('componentDidMount');
        this.props.app.setState({ popout: <ScreenSpinner /> });
        if (this.props.app.state.apiToken && this.props.app.state.apiAlbumId) {
            this.props.app.vkGetPhotos();
        } else {
            this.props.app.vkReceiveToken();
        }
    }

    render() {
        const {id} = this.props;
        console.log( this.props.app.state.photos);
        const tempPhotos= this.props.app.state.photos.filter(item=>item.sizes.length>0);
        const photos = tempPhotos.map(item => {
            return {
        	   url: item.sizes[item.sizes.length - 1].url, 
        	   vkphoto: "https://vk.com/photo"+item.owner_id+"_"+item.id,
                attachments:"photo"+item.owner_id+"_"+item.id
            };
    });
        const photosHtml = photos.map((item, i) => <Mem image={item.url} vkphoto={item.vkphoto} attachments={item.attachments} key={i}/>);
        console.log(photos);
        console.log(photosHtml);

        return (
            <Panel id={id}>
                <PanelHeader>Мои мемы</PanelHeader>
                     <FormLayout>
                        <FormLayoutGroup top="Сохранены в вашем приватном альбоме">
                {!this.props.app.state.isDisabledAccess && <Div>
                    Мемов в альбоме: {this.props.app.state.photos.length}
                </Div>}

                 {this.props.app.state.isDisabledAccess && <Div>
                  Для сохранения разрешите доступ к альбомам.
                </Div>}

                {photosHtml}
                </FormLayoutGroup>
                </FormLayout>

            </Panel>
        );
    }
}


export default Mymem;



