class Tape extends React.Component {
	constructor(props){
		super(props);
			this.state = {			
		columns: 3,
    tooltip: true,
    tooltip2: false
	}
}
componentDidMount(){
  if (localStorage.getItem("Tool") === null) {
    this.setState({tooltip: true})
    } else this.setState({tooltip: false})
  }



drawThisImage(object) {

this.props.app.setState({
selectedImage: object.photo.src,
activeStory:'create',
photoObject: object
})
}

render () {
return (
	<Panel id={this.props.id}>
		<PanelHeader>Шаблоны</PanelHeader>
		
			<Div>
      <FormLayout>
        <FormLayoutGroup top="КОМИКСЫ">
        <Tooltip
                text="Привет! Это шаблоны для твоих будущих мемов. Кликай по нужному, включай фантазию и начинай творить! "
                isShown={this.state.tooltip}
                onClose={() => {
                  this.setState({ tooltip: false, tooltip2: true})
                  localStorage.setItem("Tool", 1)

                }}
                offsetX={20}
              >
              <div className = "hint"></div>
              </Tooltip>
				<Gallery photos={PHOTO} onClick = {(index, object)=>{this.drawThisImage(object)}} columns = {this.state.columns}/>
        
        </FormLayoutGroup>

        <FormLayoutGroup top="ПЕРСОНАЖИ">
        <Tooltip
                text="А это раздел с самыми популярными шаблонами. Кликай и делай свой мем!"
                isShown={this.state.tooltip2}
                onClose={() => {
                  this.setState({ tooltip2: false })

                }}
                offsetX={10}
              >
              <div className = "hint"></div>
              </Tooltip>
				<Gallery photos={PHOTO_SET} onClick = {(index, object)=>{this.drawThisImage(object)}} columns = {this.state.columns}/>
        </FormLayoutGroup>
        </FormLayout>
			</Div>
		
	</Panel>
)
}
}
export default Tape;



